<template>
	<div class="change">
		<backbar></backbar>
		<div class="title">
			<span></span>
		</div>
		<div class="change-pwd-content">
			<div class="text_area">
				<mt-field v-model="newpassword" placeholder="" @blur.native.capture="checknewpwd()"></mt-field>	
				<mt-field type="password" v-model="newpassword2" placeholder=""></mt-field>
				<hr style="margin-left: 0.3125rem" />
			</div>
			<p class="tishi">6-20(),</p>
			<input class="checkbox" type="checkbox">
		</div>
		<div class="change-pwd-bottom">
			<mt-button type="primary" size="large" @touchstart.native="changepwd()"></mt-button>
		</div>
		
	</div>
</template>

<script>
import backbar from '../sub-components/backbar.vue'
import {Toast} from 'mint-ui'
export default {
	data() {
		return {
			newpassword:null,
			newpassword2:null,
			userId:null
		}
	},
	components:{
		backbar,
	},
	mounted() {
		this.checkId()
	},
	methods: {
		checkId(){
			this.userId = JSON.parse(localStorage.getItem('userId'))
			if(this.userId == null){
				Toast('')
			}
		},
		checknewpwd(){
			let pwd =  this.$RegExp.test(this.newpassword)
			if(!pwd){
				Toast("")
			}
		},
		changepwd(){
			if(this.newpassword2!==this.newpassword){
				Toast("")
				this.newpassword2 = null;
			}
			if(this.userId == null){
				Toast('')
			}else{
				if(this.newpassword2!==null){
					this.$axios.put(`/vms/user/updatePassword
					?password=${this.newpassword}&userId=${this.userId}`)
					.then((res)=>{
						Toast(res.data.msg)
						// 
						localStorage.removeItem('userinfo'),
						localStorage.removeItem('userId'),
						setTimeout(()=>{
							this.$router.push('/login')
						},200)
					}).catch((err)=>{
						console.log(err)
					})
				}else{
					Toast("")
				}

			}

		}
	},
}
</script>

<style lang="less" scoped>
.change{
	background-color: rgb(255, 255, 255);
	height: 17rem;
}
	.title{
		text-align: center;
		font-size: 16px;
		margin: 0.2rem;	
		padding: 0.2rem;
	}	
	.tishi{
		margin: 0 0.3rem;
		color: #888888;
	}
	.checkbox{
		margin: 0.1rem 0.3rem;
		float: left;
	}
	.change-pwd-content{
		width: 100%;
		height: 7rem;
		.text_area{
			/deep/ .mint-field-core{
				margin: 0.2rem 0;
			}
		}
	}
	.change-pwd-bottom{
		padding: 0 0.3rem;
	}
</style>
