<template>
	<div class="register">
		<div class="register-header">
			<h1></h1>
		</div>
		<mt-field label="" placeholder="" v-model="username" @blur.native.capture="checkname()"></mt-field>
		<mt-field label="" placeholder="" type="password" v-model="password1"></mt-field>
		<mt-field label="" placeholder="" type="password" v-model="password2"  @blur.native.capture="checkpwd()"></mt-field>
		<mt-field label="" placeholder="" type="email" v-model="email"></mt-field>
		<div class="zhuce">
			<mt-button type="primary" size="large" @touchstart.native="register()"></mt-button>
		</div>	
	</div>
</template>

<script>
import {Toast} from 'mint-ui'
export default{
	data(){  
		return{
			username:null,
			password1:null,
			password2:null,
			email:null,
			data:{},
			toastInstance:null
		}
	},
	methods:{
		// 
		checkname(){
			this.$axios.get(`/vms/user/getByUserName/${this.username}`)
			.then((res)=>{
				if(res.data.msg == ""){
					Toast("")
					this.username = null;
				}
			}).catch((err)=>{
				console.log(err)
			})
		},
		// 
		checkpwd(){
			if(this.password1!==this.password2){
				this.password2=null;
				Toast("")
			}
		},
		register(){
			let data = this.$RegExp.test(this.username)
			// 
			if(data){
				let pwd = this.$RegExp.test(this.password1)
				// 
				if(pwd&&this.password2!==null){
					// 
					this.$axios.get(`/vms/user/existEmail/${this.email}`).then((res)=>{
						if(res.data){
							// 
							this.$axios.post('/vms/user/register',{
								userName:this.username,
								password:this.password1,
								email:this.email
							}).then((res)=>{
								Toast(res.data.msg)
								this.$router.push('/login')
							}).catch((res)=>{
								console.log(res)
							})
						}else{
							Toast("")
						}
					}).catch((err)=>{
						console.log(err)
					})
				}else{
					Toast("+")
				}
			}else{
				Toast('')
			}
		}
	}
}
</script>

<style lang="less" scoped>
.register{
	background-color: rgb(255, 255, 255);
	.register-header{
		padding: 0.3rem;
		h1{
			text-align: center;
		}
	}
	.mint-cell{
		padding: 0.3rem;
		/deep/.mint-cell-wrapper{
			padding: 0;
		}
	}
}

/deep/.mint-field-core{
	margin: 0;
}
.zhuce{
	margin-top: 1.25rem;
	padding: 0.3rem;
	text-align: center;
}
</style>
